// стили заголовка (использовал для секции teacher)
// с другими секциями не сверял

.section-title {
  @include font(800, 20px, 26px);
  letter-spacing: 0.03em;

  @media screen and (min-width: 768px) {
    @include font(800, 26px, 34px);
  }

  @media screen and (min-width: 1200px) {
    @include font(800, 46px, 60px);
  }
}
